.Polo-footer {
  background: #FEFEFE;
  box-shadow: 0 rem(6) rem(13) rgba(0, 0, 0, 0.06);
  height: rem(140);
  min-width: 100vw;
  svg {
    position: relative;
    top: rem(-10); } }

.Home {
  h2 {
    font-size: rem(26) !important;
    @include respond-to(medium) {
      font-size: rem(32); } } }

.Make {
  padding:  3rem 2rem !important;
  @include respond-to(medium) {
    height: calc(100vh - 58px); }
  h3 {
    font-size: rem(16);
    font-weight: 100;
    color: #567A23;
    margin: rem(10);
    @include respond-to(medium) {
      margin: 0; } }
  hr {
    border: none;
    border-top: rem(2) dotted #567A23;
    transform: rotate(90deg) translateX(45px);
    transform-origin: 50% 0;
    @include respond-to(medium) {
      transform: rotate(0) translateX(0); } }
  p {
    font-size: rem(18);
    font-weight: 100;
    line-height: rem(32);
    margin: rem(28) 0;
    @include respond-to(medium) {
      font-size: rem(22);
      font-weight: 100;
      line-height: rem(40);
      margin: rem(40) 0; } } }

.Make-hr {
  width: rem(100);
  height: rem(100);
  padding: 0 rem(10);
  @include respond-to(medium) {
    width: auto;
    height: auto; } }

.Make-hr--vertical {
  left: calc(50% - 18px);
  height: rem(100);
  @include respond-to(medium) {
    position: absolute; }
  hr {
    width: rem(80);
    @include respond-to(medium) {
      width: rem(36);
      transform: rotate(90deg) translatex(16px); } } }

#Make-marketing, #Make-prototypes, #Make-changes {
  position: relative; }

#Make-prototypes svg {
  max-width: rem(184); }

#Make-changes {
  @include respond-to(medium) {
    max-width: rem(167); } }

#Make-test {
  width: rem(200); }

.Make-hrSpace {
  height: rem(26);
  display: none;
  @include respond-to(medium) {
    display: block; } }

.Make-hrSpaceTwo {
  height: rem(50); }

.Make-hrSpaceThree {
  height: rem(40); }

.Make-process {
  flex-direction: column;
  @include respond-to(medium) {
    flex-direction: row; }
  div:nth-child(4) hr {
    border-top-color: #FD7373; }
  div:nth-child(6) hr {
    border-top-color: #67BDBC; } }

@media (max-width: 36rem) {
  #Make-start {
    order: 1; }
  #Make-hr--1 {
    order: 2; }
  #Make-planner {
    order: 3; }
  #Make-hr--2 {
    order: 4; }
  #Make-marketing {
    order: 5; }
  #Make-requirements {
    order: 6; }
  #Make-hr--3 {
    order: 7; }
  #Make-sketches {
    order: 8; }
  #Make-hr--4 {
    order: 9; }
  #Make-prototypes {
    order: 10; }
  #Make-developer {
    order: 11; }
  #Make-hr--5 {
    order: 12; }
  #Make-test {
    order: 13; }
  #Make-hr--6 {
    order: 14; }
  #Make-changes {
    order: 15; }
  #Make-deploy {
    order: 16; }
  #Make-hr--7 {
    order: 17; }
  #Make-production {
    order: 18; }
  #Make-hr--8 {
    order: 19; }
  #Make-social {
    order: 20; }

  img {
    max-width: rem(250);
    @include respond-to(medium) {
      max-width: 100%; } } }
.WeDo {
  background: url(../img/back-wedo.svg) no-repeat 0 0/cover;
  max-width: 100% !important;
  width: 100% !important;
  @include respond-to(medium) {
    height: calc(100vh - 58px); }

  p {
    font-size: rem(18);
    font-weight: 100;
    line-height: rem(32);
    margin: rem(28) 0;
    @include respond-to(medium) {
      font-size: rem(22);
      font-weight: 100;
      line-height: rem(40);
      margin: rem(40) 0; } }
  ul {
    text-align: justify;
    @include respond-to(medium) {
      margin: auto; } }
  a {
    display: block;
    height: rem(56);
    line-height: rem(56);
    color: #434C5E;
    font-weight: 300;
    padding: 0 1rem;
    transition: all 0.5s;
    border-left: rem(3) solid #fff;
    font-size: 4.7vw;
    @include respond-to(medium) {
      font-size: rem(20);
      height: rem(60);
      line-height: rem(60); } }
  img {
    height: rem(250);
    margin:  0;
    @include respond-to(medium) {
      height: rem(320); } }
  li {
    transition: all 0.5s;
    span {
      color: #c6c8cc;
      transition: all 0.5s;
      position: relative;
      display: inline-block;
      padding-right: rem(50);
      &:before {
        content: '';
        position: absolute;
        border-top: rem(1) solid #c6c8cc;
        width: rem(30);
        height: rem(1);
        top: rem(30);
        left: rem(28); } }

    &:hover {
      a {
        color: #538bf4;
        border-left-color: #538bf4;
        font-weight: 300; }
      span {
        color: #538bf4;
        &:before {
          border-top-color: #538bf4; } } } } }

